.MenuItem{
    border-bottom: 1px solid rgb(0, 0, 0);
    text-align: left;
    cursor: pointer;

}

.MenuItem > h4{
    font-weight:500;
    letter-spacing: .5px;
    text-transform: uppercase;
    padding-bottom: 15px;
    font-size: 15px;
    padding-left: 15px;
    cursor: pointer;
}